<template>
  <div>
      <p>计数器</p>
      <p>当前数值：{{counter}}</p>
      <v-btn @click="increment">加一</v-btn>
      <v-btn @click="decrement">减一</v-btn>      
  </div>
</template>

<script>
export default {
    name:'dashboard-counter',
    methods:{
        increment(){
            // 通过 this.$store.commit() 调用在 mutations 中修改状态的方法
            this.$store.commit('increment', 1);
        },
        decrement(){
            this.$store.commit('increment', -1);
        }
    },
    computed:{
        // 通过计算属性获取状态值 
        counter(){
            return this.$store.state.count;
        },
    }

}
</script>

